.google-login-button {
  display: flex;
  align-items: center;
  background-color: white;
  color: #757575;
  border: 1px solid #ddd;
  width: 233px;
  height: 58px;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.google-login-button:hover {
  background-color: #4285f4;
  color: white;
}

.bc-logo {
  background-color: #ffffff;
  border-radius: 5px;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  /* margin-right: 10px; */
}

.google-logo {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}

.google-logo img {
  width: 100%;
  height: 100%;
}
